/**
 * JSONP
 * @param {string} url 
 * @param {object} options 
 * @param {Function} success 
 * @param {Function} error 
 */
export function getJSONP (url, options, success, error) {
  let js = document.createElement("script");
  let callbackKey = options.callback || "callback";
  let callbackName = "__callback" + Date.now();
  let timeout = options.timeout || 30000;
  let timing;
  function end () {
    clearTimeout(timing);
    delete window[callbackName];
    js.remove();
  }
  window[callbackName] = (res) => {
    if (typeof success === "function") {
      success(res);
    }
    end();
  };
  js.onerror = () => {
    if (typeof error === "function") {
      error();
    }
    end();
  };
  timing = setTimeout(function () {
    if (typeof error === "function") {
      error();
    }
    end();
  }, timeout);
  js.src = url + (url.indexOf("?") >= 0 ? "&" : "?") + callbackKey + "=" + callbackName;
  document.body.appendChild(js);
}
